.ai-loader {
  position: absolute;
  top: 50%;
  left: 50%;
  @include for-mobile-layout {
    top: 70%;
  }
}

.ai-loader,
.ai-loader:before,
.ai-loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation-fill-mode: both;
  animation: bblFadInOut 1.8s infinite ease-in-out;
}
.ai-loader {
  color: #fff;
  font-size: 7px;
  position: relative;
  text-indent: -9999em;
  transform: translateZ(0);
  animation-delay: -0.16s;
}
.ai-loader:before,
.ai-loader:after {
  content: '';
  position: absolute;
  top: 0;
}
.ai-loader:before {
  left: -3.5em;
  animation-delay: -0.32s;
}
.ai-loader:after {
  left: 3.5em;
}

@keyframes bblFadInOut {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
